html, body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: fixed!important;
}
/* To prevent long-press that will bring up copy-paste dialog. */
* {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-touch-callout: none;
  -moz-touch-callout: none;
  -ms-touch-callout: none;
  touch-callout: none;
  outline: 0; /* To prevent blue-box outline after click: http://stackoverflow.com/questions/21719306/getting-rid-of-a-blue-box-around-button-when-pressed */
}
textarea, input {
  -webkit-user-select: text !important;
  -moz-user-select: text !important;
  -ms-user-select: text !important;
  user-select: text !important;
}
[ng\:cloak], [ng-cloak], [data-ng-cloak], [x-ng-cloak], .ng-cloak, .x-ng-cloak {
  display: none !important;
}

body {
  /*background-color: #b9dfde;*/
  background-color: #FCFAF2;
  background-image: url(../img/texture.png);
}

img {
  position: absolute;  
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto; 
  transition: width 0.2s, height 0.2s;
  transition-timing-function: linear;
  /*border-radius: 12px;*/
}

/*img:hover {
  width:100%;
  height:100%;
}*/

a {
  text-decoration: none;
  color: lightblue;
}

a:visited {
    color: midnightblue;
}

a:hover {
    color: midnightblue;
}

a:active {
    color: midnightblue;
} 

