.selected {
  width: 100%;
  height: 100%;
  animation-name: select;
  animation-duration: 0.2s;
}

.cell {
  width:100%; 
  height: 100%; 
  box-shadow:inset 0px 0px 0px 4px #FCFAF2;
}

.disabled {
  filter:grayscale(90%);
  width: 70%;
  height: 70%;
}

.possible {
  /*border: 4px solid #326f4e;*/
  box-shadow:inset 0px 0px 0px 4px #326f4e;
  background-image: url(../img/go.png);
  background-size: contain;
  animation: slowlyAppear 0.2s linear;
}

@keyframes select {
    0%   {height: 100%;}
    50%  {height: 62%;}
    100% {height: 100%;}
}

.pieceHeight {
  height: 100px;
}

.slowlyAppear {
  animation: slowlyAppear 0.5s linear;
}

@keyframes slowlyAppear {
  from {opacity: 0.1;}
  to {opacity: 1;}
}